<template>
  <b-tr>
    <b-tr>
      <b-td>작성자 : {{ userid }} </b-td>
      <b-td class="text-right">{{ regtime | dateFormat }}</b-td>
      <b-td>
        <b-button variant="outline-primary" @click="moveModify">수정</b-button>
        <b-button variant="outline-primary" @click="moveDelete">삭제</b-button>
      </b-td>
    </b-tr>
    <b-tr class="text-left">
      <b-td>답변 : {{ content }}</b-td>
    </b-tr>
  </b-tr>
</template>

<script>
import moment from "moment";

export default {
  name: "QuestionListItem",
  props: {
    answerno: Number,
    questionno: Number,
    userid: String,
    hit: Number,
    regtime: String,
    content: String,
  },
  filters: {
    dateFormat(regtime) {
      return moment(new Date(regtime)).format("YY.MM.DD");
    },
  },
  methods: {
    moveModify() {
      //alert(this.questionno);
      this.$router.push({
        name: "answerModify",
        params: { questionno: this.questionno, answerno: this.answerno },
      });
    },
    moveDelete() {
      this.$router.push({
        name: "answerDelete",
        params: { questionno: this.questionno, answerno: this.answerno },
      });
    },
  },
};
</script>

