<template>
  <b-container class="bv-example-row mt-3">
    <b-row>
      <b-col>
        <b-alert show><h3>답글 목록</h3></b-alert>
      </b-col>
    </b-row>
    <b-row class="mb-1">
      <b-col class="text-right">
        <b-button variant="outline-primary" @click="moveWrite()"
          >답변쓰기</b-button
        >
      </b-col>
    </b-row>
    <b-row>
      <b-col v-if="articles.length">
        <b-table-simple hover responsive>
          <b-thead head-variant="dark">
            <b-tr>
              <b-th>답글</b-th>
            </b-tr>
          </b-thead>
          <tbody>
            <!-- 하위 component인 ListRow에 데이터 전달(props) -->
            <answer-list-item
              v-for="article in articles"
              :key="article.articleno"
              v-bind="article"
            />
          </tbody>
        </b-table-simple>
      </b-col>
      <b-col v-else class="text-center">질문 목록이 없습니다.</b-col>
    </b-row>
  </b-container>
</template>

<script>
import http from "@/api/http";
import AnswerListItem from "@/components/QnA/answer/item/AnswerListItem";

export default {
  name: "AnswerList",
  props: {
    questionno: Number,
  },
  components: {
    AnswerListItem,
  },
  data() {
    return {
      articles: [],
    };
  },
  created() {
    //alert(this.$route.params.articleno);
    http.get(`/aboard/${this.$route.params.articleno}`).then(({ data }) => {
      this.articles = data;
    });
  },
  methods: {
    moveWrite() {
      //alert(this.$route.params.articleno);
      this.$router.push({
        name: "answerRegister",
        params: { questionno: this.questionno },
      });
    },
  },
};
</script>

<style scope>
.tdClass {
  width: 50px;
  text-align: center;
}
.tdSubject {
  width: 300px;
  text-align: left;
}
</style>
